*{
  padding:0;
  margin:0;
}
body{
  background-color: #9266ff;
}
.App{
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin:40px auto;
}
#todoForm input{
  border:0;
  padding:0 20px;
  width: 200px;
  margin:20px;
  height: 30px;
  color:#fff;
  background-color:rgba(27,112,137);
}
#todoForm input::placeholder{
  color: rgba(255,255,255,0.5);
}

#todoForm input:focus{
 outline:none;
}

#todoForm button{
  height:30px;
  width: 70px;
  border-radius:5px;
  border:0;
  color:#fff;
  cursor:pointer;
  background-color: darkorange;
}
